<template>
  <m-page title="Danh sch doanh nghip" :rightSlot="true">
    <template v-slot:header-right>
      <button class="btn btn--default btn--refresh" @click="onAddNew">
        <i class="icofont-ui-add"></i> Thm n v
      </button>
    </template>
    <template v-slot:container>
      <m-table-layout>
        <!-- TOOLBAR -->
        <template v-slot:toolbar>
          <div class="toolbar__left">
            <m-input-search width="300px" placeholder="Tm kim" v-model="keySearch"></m-input-search>
          </div>
          <div class="toolbar__right">
            <button class="btn-icon icon icon-24 icon--mail-statistic" title="GI THNG K HM NAY" @click="sentStatistic()">
            </button>
            <button class="btn-icon icon icon-20 icon--backup" title="SAO LU D LIU" @click="backupDatabase()">
            </button>
            <button class="btn btn-refresh" title="LY D LIU MI" @click="loadData()">
              <i class="icofont-refresh"></i>
            </button>
          </div>
        </template>
        <!-- BNG D LIU -->
        <template v-slot:container>
          <m-table ref="tbOrganizations" :data="organizations" empty-text="Khng c d liu" width="100%" height="100%">
            <!-- <m-column type="index" label="#" width="50px"></m-column> -->
            <el-table-column type="selection" width="30" fixed="left" />
            <m-column prop="OrganizationName" fixed="left" label="Tn ca hng">
              <template #default="scope">
                <div class="table-cell org-name">
                  <span class="org-name__icon org--confirm" v-if="scope.row.IsConfirm"><i
                      class="icofont-check-circled"></i></span>
                  <span class="org-name__text">
                    {{ scope.row.OrganizationName }}
                  </span>
                </div>
                <div class="org-description">
                  {{ scope.row.Description }}
                </div>
              </template>
            </m-column>
            <m-column prop="Address" label="a ch" v-if="!isMobile"></m-column>
            <m-column width="50" fixed="right">
              <template #default="scope">
                <div class="button-column">
                  <button class="btn--table-mini --color-edit" :title="scope.row.OrganizationName"
                    @click="onUpdate(scope.row)">
                    <i class="icofont-ui-edit"></i>
                  </button>
                  <button v-if="!scope.row.IsConfirm" class="btn--table-mini --color-red"
                    :title="scope.row.OrganizationName" @click="onDelete(scope.row)">
                    <i class="icofont-ui-delete"></i>
                  </button>
                </div>
              </template>
            </m-column>
          </m-table>
        </template>
        <!-- PHN TRANG -->
        <template v-slot:footer>
          <m-paging entityName="ca hng" :totalRecords="totalRecords" :pageSizeData="[10, 20, 50, 100, 200]"
            v-model:limit="limit" v-model:offset="offset" @onChangePagesize="loadData" @onPrevPage="loadData"
            @onNextPage="loadData" @onInitPaging="onInitPaging"></m-paging>
        </template>
      </m-table-layout>
    </template>
    <template v-slot:footer> </template>
  </m-page>
  <div v-if="showDeleteProgress" class="delete-info">
    <div class="delete-item" v-for="(info, index) in deleteInfos" :key="index">
      <div v-if="info.IsDeleted" class="delete__icon delete__icon--checked">
        <i class="icofont-checked"></i>
      </div>
      <div v-else class="delete__icon delete__icon--unchecked">
        <i class="icofont-spinner"></i>
      </div>
      <div class="delete__text">
        <span v-if="info.IsDeleted"> xa</span><span v-else>ang xa</span>
        <span>{{ info.ObjectText }}</span>
      </div>
    </div>
  </div>
</template>
<script>
import debounce from "@/scripts/debounce";
import router from '@/router';
import commonJs from "@/scripts/common";
export default {
  name: "OrganizationList",
  components: {},
  emits: [],
  props: [],
  computed: {
    isMobile: function () {
      if (this.innerWidth < 720) {
        return true;
      }
      return false;
    }
  },
  created() {
    this.innerWidth = window.innerWidth;
    window.addEventListener("resize", this.setWindownWith);
    this.deleteInfos_Original = JSON.stringify(this.deleteInfos);
    this.hubConnection.on(
      "RecieveRemoveOrganizationInfo",
      (progressName, isDone) => {
        for (const item of this.deleteInfos) {
          if (item.ObjectName == progressName) {
            item.IsDeleted = true;
            break;
          }
        }
        if (isDone) {
          this.loadData();
        }
      }
    );
  },
  watch: {
    keySearch: debounce(function () {
      this.loadData();
    }, 500),
  },
  methods: {
    setWindownWith() {
      var width = window.innerWidth;
      this.innerWidth = width;
    },
    onInitPaging(startIndex, endIndex, pageSize) {
      this.limit = pageSize;
      this.offset = startIndex - 1;
      this.loadData();
    },
    loadData() {
      var key = this.keySearch;
      this.maxios
        .get(
          `organizations/paging?limit=${this.limit}&offset=${this.offset}&key=${key || ""
          }`
        )
        .then((res) => {
          this.organizations = res.Data;
          this.totalRecords = res.TotalRecords;
        });
    },
    onAddNew() {
      router.push(`/admin/organizations/create`);
    },
    onUpdate(organization) {
      router.push(`/admin/organizations/${organization.OrganizationId}`)
    },
    onDelete(organization) {
      // Reset laij thng tin cc object xa:
      this.deleteInfos = JSON.parse(this.deleteInfos_Original);
      this.commonJs.showConfirm(
        `Bn c chc chn mun xa [<b>${organization.OrganizationName}</b>] khng? <br>
        Sau khi xa ton b d liu s b mt v khng th khi phc.`,
        () => {
          this.showDeleteProgress = true;
          this.maxios
            .delete(`organizations/${organization.OrganizationId}`)
            .then(() => {
              setTimeout(() => {
                this.showDeleteProgress = false;
              }, 2000);
            })
            .catch(() => {
              this.showDeleteProgress = false;
            });
          this.loadData();
        }
      );
    },
    backupDatabase() {
      this.commonJs.showConfirm(
        `Thc hin sao lu d liu? <br>
        Qu trnh c th mt vi pht.`,
        () => {
          this.maxios
            .post(`organizations/backup`);
        }
      );
    },
    sentStatistic(){
      this.commonJs.showConfirm(
        `Vic ny s thc hin gi bo co doanh thu t ng trong <b>ngy hm nay (${commonJs.formatDate(new Date())})</b> cho ton b cc n v. <br>
        Qu trnh c th mt vi pht. <br>
        Nhn <b>[ng ]</b>  tin hnh!`,
        () => {
          const date = new Date();
          const dateString = date.getDate();
          const monthString = date.getMonth() +1;
          const year = date.getFullYear();
          this.maxios
            .get(`organizations/statistics?date=${year}-${monthString}-${dateString}&sentEmail=true`);
        }
      );
    }
  },
  beforeUnmount() {
    this.hubConnection.off("RecieveRemoveOrganizationInfo");
    window.removeEventListener("resize", this.setWindownWith);
  },
  data() {
    return {
      innerWidth: 0,
      organizations: [],
      totalRecords: 0,
      isReload: false,
      keySearch: "",
      limit: 0,
      offset: 0,
      idSelected: null,
      showDeleteProgress: false,
      deleteInfos_Original: "",
      deleteInfos: [
        {
          IsDeleted: false,
          ObjectName: "InvoiceDetails",
          ObjectText: "Chi tit ha n",
        },
        { IsDeleted: false, ObjectName: "Invoices", ObjectText: "Ha n" },
        { IsDeleted: false, ObjectName: "Products", ObjectText: "Sn phm" },
        {
          IsDeleted: false,
          ObjectName: "GroupProducts",
          ObjectText: "Nhm sn phm",
        },
        { IsDeleted: false, ObjectName: "Units", ObjectText: "n v tnh" },
        {
          IsDeleted: false,
          ObjectName: "Customers",
          ObjectText: "D liu khch hng",
        },
        {
          IsDeleted: false,
          ObjectName: "CustomerGroups",
          ObjectText: "Nhm khch hng",
        },
        {
          IsDeleted: false,
          ObjectName: "Positions",
          ObjectText: "Chc v, v tr",
        },
        { IsDeleted: false, ObjectName: "UserRoles", ObjectText: "Quyn User" },
        { IsDeleted: false, ObjectName: "Users", ObjectText: "Ngi dng" },
        {
          IsDeleted: false,
          ObjectName: "Employees",
          ObjectText: "D liu nhn vin",
        },
        {
          IsDeleted: false,
          ObjectName: "MSLicenses",
          ObjectText: "Thng tin License",
        },
        {
          IsDeleted: false,
          ObjectName: "Organizations",
          ObjectText: "Thng tin n v",
        },
      ],
    };
  },
};
</script>
<style scoped>
.delete-info {
  position: fixed;
  bottom: 5px;
  right: 5px;
  z-index: 101;
  background-color: #fff;
  box-shadow: 0 0 5px #c1c1c1;
  border-radius: 4px;
}

.delete-item {
  background-color: #fff;
  padding: 4px 5px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  column-gap: 5px;
}

.delete-item+.delete-item {
  margin-top: 4px;
}

.delete__icon {
  font-size: 24px;
}

.delete__icon--checked {
  color: #006207;
}

.delete__icon--unchecked {
  animation: spin 3000ms infinite linear;
  -webkit-animation: spin 3000ms infinite linear;
}

.table-cell {
  display: flex;
  column-gap: 5px;
  align-items: center;
}

.org--confirm i {
  color: #006207;
  font-weight: 700;
  font-size: 16px;
}

.org--confirm+.org-name__text {
  color: #006207;
  font-weight: 700;
}

.org-description {
  font-size: 12px;
}

.org-description::before {
  content: "- ";
}
</style>
    