<template>
    <m-dialog title="Thng tin n v tnh" @onClose="onClose">
      <template v-slot:content>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="M n v"
              ref="txtUnitName"
              v-model="unit.UnitCode"
              :required="true"
              :isDisabled="false"
              :isFocus="true"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Tn n v"
              ref="txtUnitName"
              v-model="unit.UnitName"
              :required="true"
              :isDisabled="false"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <m-text-area label="M t" v-model="unit.Description"></m-text-area>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-checkbox id="chkIsSystem" :disabled="!isAdministrator" v-model="unit.IsSystem" label="n v h thng"></m-checkbox>
          </div>
        </div>
      </template>
      <template v-slot:footer>
        <button class="btn btn--close" @click="onClose">Hy</button>
        <button class="btn btn--default mg-left-10" @click="onSave">Lu</button>
      </template>
    </m-dialog>
  </template>
  <script>
  import Enum from "@/scripts/enum";
  export default {
    name: "UnitDetail",
    emits: ["onClose"],
    props: ["id"],
    computed: {
      formMode: function () {
        if (this.id) {
          return Enum.FormMode.UPDATE;
        } else {
          return Enum.FormMode.ADD;
        }
      },
      isAdministrator: function(){
        var role = localStorage.getItem("userRoleValue");
        if (role == Enum.Role.Administrator) {
          return true;
        }else{
          return false;
        }
      }
    },
    created() {
      if (this.id) {
        this.maxios.get(`units/${this.id}`).then((res) => {
          this.unit = res;
        });
      }else{
        this.maxios.get(`units/new-code`).then((res) => {
          this.unit.UnitCode = res;
        });
      }
    },
    methods: {
      onClose() {
        this.$emit("onClose");
      },
      onSave() {
        if (this.formMode == Enum.FormMode.ADD) {
          this.maxios.post(`units`, this.unit).then(() => {
            this.$emit("onClose");
          });
        } else {
          this.maxios.patch(`units/${this.id}`, this.unit).then(() => {
            this.$emit("onClose");
          });
        }
      },
    },
    data() {
      return {
        unit: {},
      };
    },
  };
  </script>
  <style scoped>
  </style>