<template>
  <m-dialog title="i mt khu" @onClose="onClose">
    <template v-slot:content>
      <form action="">
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Ti khon ng nhp:"
              ref="txtUserName"
              :modelValue="user.UserName"
              :required="true"
              :isDisabled="true"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Mt khu hin ti"
              ref="txtPassword"
              type="password"
              v-model="user.Password"
              :required="true"
              :isDisabled="false"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Mt khu mi"
              ref="txtPassword"
              type="password"
              v-model="user.NewPassword"
              :required="true"
              :isDisabled="false"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Xc nhn mt khu mi"
              ref="txtPassword"
              type="password"
              v-model="user.ReNewPassword"
              :required="true"
              :isDisabled="false"
            >
            </m-input>
          </div>
        </div>
      </form>
    </template>
    <template v-slot:footer>
      <button class="btn btn--close" @click="onClose">
        <i class="icofont-ui-close"></i> Hy
      </button>
      <button class="btn btn--default mg-left-10" @click="onSave">
        <i class="icofont-key"></i> i mt khu
      </button>
    </template>
  </m-dialog>
</template>
<script>
import Enum from "@/scripts/enum";
export default {
  name: "ChangePassword",
  emits: ["onClose"],
  props: ["id"],
  computed: {
    formMode: function () {
      return Enum.FormMode.UPDATE;
    },
  },
  created() {
    this.user.UserName = localStorage.getItem("userName");
  },
  methods: {
    onClose() {
      this.$emit("onClose");
    },
    onSave() {
      this.maxios.put(`accounts/change-password`, this.user).then(() => {
        this.commonJs.showMessenger({
          title: "i mt khu thnh cng",
          msg:
            "Bn  thay i mt khu thnh cng. Bn c th s dng mt khu mi cho ln ng nhp k tip.",
          type: Enum.MsgType.Success,
          confirm: () => {
            this.$emit("onClose");
          },
        });
      });
    },
  },
  data() {
    return {
      user: {},
    };
  },
};
</script>
<style scoped></style>
