import axios from "axios";
import router from "@/router";
import store from "@/store";
import { AUTH_LOGOUT } from "@/store/actions/auth";
import commonJs from "@/scripts/common";
import MISAEnum from "@/scripts/enum";

const apiCall = ({ url, data, method, showToast, showMsg, callback }) =>
    new Promise((resolve, reject) => {
        try {
            axios.defaults.baseURL = `${process.env.VUE_APP_BASE_URL}`;
            var token = localStorage.getItem("user-token");
            if (token)
                axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;
            if (showMsg == undefined) {
                showMsg = true;
            }
            commonJs.showLoading();
            axios({
                    method: method || "GET",
                    url: url,
                    data: data,
                })
                .then((res) => {
                    resolve(res.data);
                    if (method && method != "GET") {
                        if (showToast == undefined) {
                            showToast = true;
                        }
                        if (window.location.pathname != "/login" && showToast)
                            commonJs.showToast(`Thnh cng!`, MISAEnum.MsgType.Success);
                    }
                    if (callback) {
                        callback();
                    }
                    commonJs.hideLoading();
                })
                .catch((res) => {
                    try {
                        if (!res.response) {
                            console.log(res);
                            commonJs.showMessenger({
                                title: "Li",
                                msg: "Khng th x l yu cu ny, vui lng lin h QTV  c tr gip.",
                                type: MISAEnum.MsgType.Error,
                                confirm: callback,
                            });
                        } else {
                            var statusCode = res.response.status;
                            if (showMsg)
                                if (statusCode == 0) {
                                    commonJs.showMessenger({
                                        title: "Li kt ni",
                                        msg: "Khng th kt ni n my ch, vui lng th li sau",
                                        type: MISAEnum.MsgType.Error,
                                        confirm: callback,
                                    });
                                }
                            if (statusCode == 400) {
                                var response = {
                                    userMsg: res.response.data.UserMsg,
                                    errors: res.response.data.errors,
                                    status: 400,
                                };
                                if (response.errors && response.errors.ValidErrors) {
                                    commonJs.showErrorMessenger(
                                        "D liu khng hp l.",
                                        response.errors.ValidErrors,
                                        callback
                                    );
                                } else {
                                    var error = response.errors;
                                    var errorsMsg = [];
                                    for (const key in error) {
                                        if (Object.hasOwnProperty.call(error, key)) {
                                            const errorsArray = error[key];
                                            if (errorsArray) {
                                                for (const msg of errorsArray) {
                                                    errorsMsg.push(msg);
                                                }
                                            }
                                        }
                                    }
                                    commonJs.showErrorMessenger(
                                        "D liu khng hp l.",
                                        errorsMsg,
                                        callback
                                    );
                                }
                                reject(response);
                            } else {
                                if (statusCode == 500) {
                                    res.devMsg = res.message;
                                    res.message = res.response.data.UserMsg;
                                    res.message =
                                        res.message ||
                                        "C li pha my ch, vui lng lin h Qun tr vin  c tr gip.";
                                    console.log(res);
                                    commonJs.showErrorMessenger("Li my ch", res.message);
                                }

                                if (statusCode == 524) {
                                    res.message =
                                        res.message ||
                                        "Qu trnh x l cn nhiu thi gian v s sm hon thnh. Bn c th lm vic khc trong khi qu trnh x l hon tt.";
                                    commonJs.showMessenger({
                                        title: "",
                                        msg: res.message,
                                        type: MISAEnum.MsgType.Info,
                                    });
                                }
                                if (statusCode == 401) {
                                    if (res.config.url.includes("/login")) {
                                        res.message =
                                            "Tn ti khon hoc mt khu khng ng, vui lng kim tra li.";
                                        commonJs.showErrorMessenger(
                                            "Sai thng tin ng nhp",
                                            res.message
                                        );
                                    } else {
                                        res.message =
                                            "Phin lm vic  ht hn, bn cn ng nhp  s dng tnh nng ny.";
                                        commonJs.showMessenger({
                                            title: "",
                                            msg: res.message,
                                            type: MISAEnum.MsgType.Info,
                                            confirm: function() {
                                                store.dispatch(AUTH_LOGOUT).then(() => {
                                                    router.push("/login");
                                                });
                                            },
                                        });
                                    }
                                }
                                if (statusCode == 403) {
                                    res.message = res.response?.data?.UserMsg;
                                    if (!res.message)
                                        res.message =
                                        "Bn b gii hn quyn truy cp ti nguyn, vui lng lin h qun tr vin  c tr gip.";
                                    commonJs.showErrorMessenger(
                                        "Yu cu b t chi.",
                                        res.message
                                    );
                                }
                                if (statusCode == 404) {
                                    res.message =
                                        "H thng khng tm thy dch v cung cp cho bn, vui lng quay li sau hoc lin h qun tr  c tr gip!";
                                    commonJs.showErrorMessenger("Li dch v", res.message);
                                }
                            }
                        }
                        reject(res.response);
                        commonJs.hideLoading();
                    } catch (error) {
                        console.log(`error`, error);
                        reject(res.response);
                        commonJs.hideLoading();
                    }
                });
        } catch (err) {
            reject(new Error(err));
            commonJs.hideLoading();
        }
    });

export default apiCall;