<template>
  <div class="combobox-wrapper">
    <label v-if="label" for=""
      >{{ label }} <span v-if="required">(<span class="required">*</span>)</span>:</label
    >
    <div class="combobox">
      <span
        v-if="isShowClear && !isDisabled"
        class="clear-button"
        @click="clearButtonOnClick"
        ><i class="icofont-close"></i
      ></span>
      <input
        type="text"
        class="input combobox__input"
        ref="minput"
        v-model="textInput"
        :placeholder="placeholder"
        @input="inputOnChange"
        :disabled="isDisabled"
        @keydown="selecItemUpDown"
      />
      <button
        v-if="!isDisabled"
        class="button combobox__button"
        @click="btnSelectDataOnClick"
        type="button"
        @keydown="selecItemUpDown"
        tabindex="-1"
        :disabled="isDisabled"
      >
        <!-- <i class="fa-solid fa-chevron-down"></i> -->
        <img :src="require('./icon/down.png')" alt="" srcset="" width="24" height="24" />
      </button>
      <div
        v-if="isShowListData"
        class="combobox__data"
        ref="combobox__data"
        v-clickoutside="hideListData"
      >
        <a
          class="combobox__item"
          v-for="(item, index) in dataFilter"
          :class="{
            'combobox__item--focus': index == indexItemFocus,
            'combobox__item--selected':
              index == indexItemSelected || modelValue == item[this.propValue],
          }"
          :key="item[this.propValue]"
          :ref="'toFocus_' + index"
          :value="item[this.propValue]"
          @click="itemOnSelect(item, index)"
          @focus="saveItemFocus(index)"
          @keydown="selecItemUpDown(index)"
          @keyup="selecItemUpDown(index)"
          tabindex="1"
        >
          <div class="combobox__item-icon">
            <img
              v-show="index == indexItemSelected"
              :src="require('./icon/check.png')"
              alt=""
              srcset=""
              width="14"
              height="11"
            />
          </div>
          <span>{{ item[this.propText] }}</span>
        </a>
      </div>
      <div v-if="showLoadingData" class="combobox-loading"></div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
/**
 * Gn s kin nhn click chut ra ngoi combobox data (n data list i)
 * NVMANH (31/07/2022)
 */
const clickoutside = {
  mounted(el, binding, vnode, prevVnode) {
    el.clickOutsideEvent = (event) => {
      // Nu element hin ti khng phi l element ang click vo
      // Hoc element ang click vo khng phi l button trong combobox hin ti th n i.
      if (
        !(
          (
            el === event.target || // click phm vi ca combobox__data
            el.contains(event.target) || //click vo element con ca combobox__data
            el.previousElementSibling.contains(event.target)
          ) //click vo element button trc combobox data (nhn vo button th hin th)
        )
      ) {
        // Thc hin s kin ty chnh:
        binding.value(event, el);
        // vnode.context[binding.expression](event); // vue 2
      }
    };
    document.body.addEventListener("click", el.clickOutsideEvent);
  },
  beforeUnmount: (el) => {
    document.body.removeEventListener("click", el.clickOutsideEvent);
  },
};

function removeVietnameseTones(str) {
  if (str == null || str == undefined) {
    return "";
  }
  str = str.replace(/||||||||||||||||/g, "a");
  str = str.replace(/||||||||||/g, "e");
  str = str.replace(/||||/g, "i");
  str = str.replace(/||||||||||||||||/g, "o");
  str = str.replace(/||||||||||/g, "u");
  str = str.replace(/||||/g, "y");
  str = str.replace(//g, "d");
  str = str.replace(/||||||||||||||||/g, "A");
  str = str.replace(/||||||||||/g, "E");
  str = str.replace(/||||/g, "I");
  str = str.replace(/||||||||||||||||/g, "O");
  str = str.replace(/||||||||||/g, "U");
  str = str.replace(/||||/g, "Y");
  str = str.replace(//g, "D");
  // Some system encode vietnamese combining accent as individual utf-8 characters
  // Mt vi b encode coi cc du m, du ch nh mt k t ring bit nn thm hai dng ny
  str = str.replace(/\u0300|\u0301|\u0303|\u0309|\u0323/g, ""); //       huyn, sc, ng, hi, nng
  str = str.replace(/\u02C6|\u0306|\u031B/g, ""); //     , , , , 
  // Remove extra spaces
  // B cc khong trng lin nhau
  str = str.replace(/ + /g, " ");
  str = str.trim();
  // Remove punctuations
  // B du cu, k t c bit
  str = str.replace(
    /!|@|%|\^|\*|\(|\)|\+|\=|\<|\>|\?|\/|,|\.|\:|\;|\'|\"|\&|\#|\[|\]|~|\$|_|`|-|{|}|\||\\/g,
    " "
  );
  return str;
}

const keyCode = {
  Enter: 13,
  ArrowUp: 38,
  ArrowDown: 40,
  ESC: 27,
};

export default {
  name: "MSCombobox",
  directives: {
    clickoutside,
  },
  props: [
    "value",
    "label",
    "modelValue",
    "url",
    "propValue",
    "propText",
    "isLoadData",
    "placeholder",
    "required",
    "isDisabled",
    "isFocus",
    "firstSelected"
  ],
  emits: ["onSelected", "getValue", "update:modelValue", "onChangeText"],
  methods: {
    /**
     * Lu li index ca item  focus
     * NVMANH (31/07/2022)
     */
    saveItemFocus(index) {
      this.indexItemFocus = index;
    },

    /**
     * n danh sch item
     * NVMANH (31/07/2022)
     */
    hideListData() {
      this.isShowListData = false;
    },

    /**
     * Nhn vo button th hin th hoc n List Item
     * NVMANH (31/07/2022)
     */
    btnSelectDataOnClick() {
      // event.stopPropagation();
      this.dataFilter = this.data;
      this.isShowListData = !this.isShowListData;
    },

    /**
     * Click chn item trong danh sch
     * NVMANH (31/07/2022)
     */
    itemOnSelect(item, index) {
      const text = item[this.propText];
      const value = item[this.propValue];
      this.textInput = text; // Hin th text ln input.
      this.indexItemSelected = index;
      this.isShowListData = false;
      this.$emit("getValue", value, text, item);
      this.$emit("update:modelValue", value);
    },

    /**
     * Nhp text th thc hin filter d liu v hin th
     * NVMANH (31/07/2022)
     */
    inputOnChange() {
      var me = this;
      // Thc hin lc cc phn t ph hp trong data:
      this.dataFilter = this.data.filter((e) => {
        let text = removeVietnameseTones(me.textInput).toLowerCase().replace(" ", "");
        let textOfItem = removeVietnameseTones(e[me.propText])
          .toLowerCase()
          .replace(" ", "");
        return textOfItem.includes(text);
      });
      this.isShowListData = true;
    },

    /**
     * La chn item bng cch nhn cc phm ln, xung trn bn phm
     * NVMANH (31/07/2022)
     */
    selecItemUpDown() {
      var me = this;
      var keyCodePress = event.keyCode;
      var elToFocus = null;
      switch (keyCodePress) {
        case keyCode.ESC:
          this.isShowListData = false;
          break;
        case keyCode.ArrowDown:
          this.isShowListData = true;
          elToFocus = this.$refs[`toFocus_${me.indexItemFocus + 1}`];
          if (this.indexItemFocus == null || !elToFocus || elToFocus.length == 0) {
            this.indexItemFocus = 0;
          } else {
            this.indexItemFocus += 1;
          }
          break;
        case keyCode.ArrowUp:
          this.isShowListData = true;
          elToFocus = this.$refs[`toFocus_${me.indexItemFocus - 1}`];
          if (this.indexItemFocus == null || !elToFocus || elToFocus.length == 0) {
            this.indexItemFocus = this.dataFilter.length - 1;
          } else {
            this.indexItemFocus -= 1;
          }
          break;
        case keyCode.Enter:
          elToFocus = this.$refs[`toFocus_${me.indexItemFocus}`];
          if (elToFocus && elToFocus.length > 0) {
            elToFocus[0].click();
            this.isShowListData = false;
          }
          break;
        default:
          break;
      }
    },
    loadData() {
      // Thc hin ly d liu t api:
      var me = this;
      const token = localStorage.getItem("user-token");
      const headers = new Headers();
      var baseUrl = process.env.VUE_APP_BASE_URL;
      var url = this.url;
      if (baseUrl) url = baseUrl + url;
      headers.append("Authorization", token);
      this.showLoadingData = true;
      fetch(url, { headers })
        .then((res) => res.json())
        .then((res) => {
          this.data = res;
          this.dataFilter = res;
          if (this.firstSelected && this.dataFilter.length > 0) {
            var value = this.dataFilter[0][this.propValue];
            this.$emit("update:modelValue",value);
          }
          this.setTextForInput();
          this.showLoadingData = false;
        })
        .catch((res) => {
          console.log(res);
          this.showLoadingData = false;
        });
    },

    /**
     * Thc hin set text hin th tng ng vi item c chn
     * Author: NVMANH(24/08/2022)
     */
    setTextForInput() {
      var me = this;
      var valueSetSelected = me.modelValue;
      if (valueSetSelected == 0 || valueSetSelected) {
        for (let index = 0; index < me.data.length; index++) {
          const element = me.data[index];
          const value = element[me.propValue];
          if (value != null && value == valueSetSelected) {
            me.textInput = element[me.propText];
            me.indexItemSelected = index;
            this.$emit("onSelected", value, me.textInput, element);
            break;
          }
        }
      } else {
        this.textInput = null;
        this.isShowClear = false;
        this.indexItemSelected = null;
        this.indexItemFocus = null;
        this.$emit("onSelected", null, null, null);
      }
    },
    clearButtonOnClick() {
      this.textInput = null;
      this.isShowClear = false;
      this.indexItemSelected = null;
      this.indexItemFocus = null;
      this.$emit("update:modelValue", null);
    },
    setFocusDefault() {
      if (this.isFocus == true) {
        this.$nextTick(function () {
          this.$refs.minput.focus();
        });
      }
    },
    setFocus() {
      this.$nextTick(function () {
        this.$refs.minput.focus();
      });
    },
  },
  watch: {
    modelValue: function (newValue, oldValue) {
      this.setTextForInput();
    },
    textInput: function (newValue) {
      if (newValue) {
        this.isShowClear = true;
      } else {
        this.isShowClear = false;
      }
      this.$emit("onChangeText", newValue);
    },
    showLoadingData: function (newValue) {
      if (!this.isDisabled && !newValue) this.setFocusDefault();
    },
  },
  created() {
    this.loadData();
  },
  mounted() {},
  data() {
    return {
      data: [], // data gc
      textInput: null, // Text hin th trong input ca item c chn
      dataFilter: [], // data  c filter
      isShowListData: false, // Hin th list data hay khng
      indexItemFocus: null, // Index ca item focus hin ti
      indexItemSelected: null, // Index ca item c selected
      showLoadingData: false,
      isShowClear: false,
    };
  },
};
</script>
<style scoped>
.required {
  color: #ff0000;
}
.combobox {
  position: relative;
  /* flex-direction: row; */
  border-radius: 4px;
  box-sizing: border-box;
}

.combobox__input,
select {
  width: 100%;
  height: 36px;
  flex: 1;
  padding-right: 56px !important;
  padding-left: 16px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.combobox__input:focus,
.combobox__input:focus ~ .combobox__button {
  border-color: #3395ff;
}

.combobox-wrapper label + .combobox {
  margin-top: 4px;
}

.combobox__button {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(90, 90, 90);
  border-radius: 0 4px 4px 0;
  right: 0px;
  top: 0px;
  border: 1px solid #bbbbbb;
  border-left: unset;
  height: 36px;
  width: 36px;
  border-radius: 0 4px 4px 0px;
  background-color: #fff;
  min-width: unset !important;
  outline: none;
  box-sizing: border-box;
  opacity: 0.5;
  cursor: pointer;
}

.combobox__button:hover,
.combobox__button:focus {
  background-color: #e6f2ff;
  color: #000;
}

.combobox__button:disabled {
  pointer-events: none;
}

.combobox__data {
  display: flex;
  flex-direction: column;
  padding: 4px 0px;
  position: absolute;
  width: 100%;
  top: 100%;
  right: 0;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 3px 6px #00000016;
  z-index: 999;
  max-height: 200px;
  min-width: 200px;
  overflow-y: auto;
}

.combobox__item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 10px;
  padding: 10px 10px 10px 10px;
  outline: none;
  cursor: pointer;
}
.combobox__item+.combobox__item{
}

.combobox__item-icon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: 16px;
  font-size: 16px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.combobox__item-icon--selected {
  width: 14px;
  height: 11px;
}

.combobox__item:hover,
.combobox__item:focus,
.combobox__item--focus,
.combobox__item--hover {
  background-color: #e6f2ff;
  color: #000;
}

.combobox__item--selected {
  pointer-events: none;
  background-color: #0062cc;
  color: #fff;
}

.combobox-loading {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(-45deg, #b7b7b7, #dddddd, #6d6d6d, #ffffff);
  animation: gradient 4s alternate infinite;
  border-radius: 4px;
  -webkit-animation: gradient 4s alternate infinite;
  -moz-animation: gradient 10s ease infinite;
  background-size: 200%;
  opacity: 0.7;
}
.clear-button {
  display: block;
  position: absolute;
  right: 36px;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 2px;
  line-height: 12px;
  font-size: 12px;
  text-align: center;
  color: #fff;
  border-radius: 50%;
  background-color: #7e7e7e;
  cursor: pointer;
}
@keyframes moveGradient {
  50% {
    background-position: 100% 50%;
  }
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-moz-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
</style>
