                :required="false"
              >
              </m-input>
            </div>
            <div class="m-col"></div>
          </div>
          <div class="m-row">
            <m-input label="a ch" v-model="user.Employee.Address"> </m-input>
          </div>
        </div>
        <!-- THNG TIN CNG VIC -->
        <div class="form-group">
          <div class="group__title">Thng tin cng vic</div>
          <div class="m-row">
            <div class="m-col">
              <m-combobox
                label="V tr"
                ref="cbxPosition"
                v-model="user.Employee.PositionId"
                url="/positions"
                propText="PositionName"
                propValue="PositionId"
              ></m-combobox>
            </div>
            <div class="m-col">
              <label for="">Ngy bt u lm vic:</label>
              <el-date-picker
                v-model="user.Employee.JoinDate"
