                v-model="user.Employee.BankAccountNumber"
              >
              </m-input>
            </div>
            <div class="m-col"></div>
          </div>
          <div class="m-row">
            <div class="m-col">
              <m-input label="Ngn hng" v-model="user.Employee.BankName"> </m-input>
            </div>
            <div class="m-col">
              <m-input
                label="Chi nhnh ngn hng"
