        </div>
        <!-- THNG TIN TI KHON -->
        <div class="form-group">
          <div class="group__title">Thng tin ti khon</div>
          <div class="m-row">
            <div class="m-col">
              <m-input
                label="Ti khon ng nhp"
                :modelValue="user.UserName"
                ref="txtUserName"
                :disabled="true"
              >
              </m-input>
            </div>
            <div class="m-col">
              <m-combobox
                label="Quyn trn Website"
                :modelValue="roleId"
                url="/roles"
                propText="OtherName"
                propValue="RoleId"
                :isDisabled="true"
              ></m-combobox>
            </div>
          </div>
          <!-- <div class="m-row">
            <div class="m-col">
              <m-input
                label="Mt khu"
                type="password"
                ref="txtPassword"
                :required="true"
                :validated="isValidated"
                v-model="user.Password"
              >
              </m-input>
            </div>
            <div class="m-col">
              <m-input
                label="Xc nhn mt khu"
                type="password"
                ref="txtRePassword"
                :required="true"
                :validated="isValidated"
                v-model="user.RePassword"
              >
              </m-input>
            </div>
          </div> -->
        </div>
      </div>
    </template>
    <template v-slot:footer>
      <div class="footer__button">
        <!-- <button class="btn btn--close" @click="onClose">Hy</button> -->
        <button class="btn btn--default mg-left-10" :disabled="!isChange" @click="onSave"><i class="icofont-ui-check"></i> Lu</button>
      </div>
    </template>
  </m-page>
</template>
<script>
import Enum from "@/scripts/enum";
import router from "@/router";
export default {
  name: "AccountInfo",
  emits: ["onClose"],
  props: ["id"],
  computed: {
    formMode: function () {
      return Enum.FormMode.UPDATE;
    },
    roleId: function () {
      if (this.user.Roles) {
        return this.user.Roles[0].RoleId;
      }
      return null;
    },
    isChange: function(){
        var newUseJson = JSON.stringify(this.user);
        if (newUseJson != this.origilUser) {
            return true;
        }else{
            return false;
        }
    }
  },
  created() {
    var userId = localStorage.getItem("user-id");
    if (userId) {
      this.maxios.get(`accounts/${userId}`).then((res) => {
        this.user = res;
        this.origilUser = JSON.stringify(res);
      });
    }
  },
  methods: {
    onClose() {
      router.push("/");
    },
    onSave() {
      this.maxios.put(`employees/${this.user.Employee.EmployeeId}`, this.user.Employee);
    },
  },
  data() {
    return {
      user: { Employee: {} },
      origilUser:null,
    };
  },
};
</script>
<style scoped>
.emp-container {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  padding-bottom: 10px;
}

.group__title {
  text-transform: uppercase;
  font-weight: 600;
  border-bottom: 1px dashed #dedede;
  border-top: 1px dashed #dedede;
  margin-bottom: 10px;
  padding: 5px 0;
  box-shadow: 0px 0px 5px #8e8e8e;
}

.footer__button{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 10px 0;
    border-top: 1px dashed #dedede;
}
</style>
