<template>
  <m-page title="ng k">
    <template v-slot:header-right></template>
    <template v-slot:container>
      <!-- BC 1 - NHP THNG TIN C NHN -->
      <div v-show="stepRegister == 1" id="step-1" class="user-info">
        <div class="m-row">
          <div class="m-col" style="width: 100px">
            <m-input
              label="Ti khon ng nhp"
              ref="txtUserName"
              v-model="user.UserName"
              :validated="isValidated"
              :required="true"
              :isFocus="false"
            >
            </m-input>
          </div>
          <div class="m-col flex-1"></div>
        </div>
        <div class="m-row">
          <div class="m-col" style="width: 100px">
            <m-input
              label="Mt khu"
              ref="txtPassword"
              type="password"
              v-model="user.Password"
              :validated="isValidated"
              :required="true"
              :isFocus="true"
            >
            </m-input>
          </div>
          <div class="m-col flex-1">
            <m-input
              label="Xc nhn mt khu"
              type="password"
              ref="txtRePassword"
              v-model="user.RePassword"
              :validated="isValidated"
              :required="true"
              :isFocus="true"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="H v tn"
              ref="txtFullName"
              v-model="user.FullName"
              :required="true"
              :isDisabled="false"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <label for="">Ngy sinh:</label>
            <el-date-picker v-model="user.DateOfBirth" type="date" format="DD-MM-YYYY" />
          </div>
          <div class="m-col">
            <m-combobox
              label="Gii tnh"
              v-model="user.Gender"
              url="/dictionarys/gender"
              propText="Text"
              propValue="Value"
            ></m-combobox>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="S in thoi"
              ref="txtUserPhoneNumber"
              v-model="user.PhoneNumber"
              :isDisabled="false"
              :required="true"
              :onlyNumberChar="true"
            >
            </m-input>
          </div>
          <div class="m-col">
            <m-input
              label="Email"
              ref="txtUserEmail"
              v-model="user.Email"
              placeholder="VD: nvmanh@nmanh.com"
              :isDisabled="false"
              :required="true"
            >
            </m-input>
          </div>
        </div>
      </div>
      <!-- BC 2 - NHP THNG TIN N V -->
      <div v-show="stepRegister == 2" id="step-2" class="organization-info">
        <div class="m-row">
          <div class="m-col" style="width: 100px">
            <m-input
              label="M ca hng"
              ref="txtOrganizationCode"
              v-model="organization.OrganizationCode"
              :validated="isValidated"
              :required="true"
              :disabled="false"
              :isFocus="false"
            >
            </m-input>
          </div>
          <div class="m-col flex-1"></div>
        </div>
        <div class="m-row">
          <div class="m-col flex-1">
            <m-input
              label="Tn ca hng"
              ref="txtOrganizationName"
              v-model="organization.OrganizationName"
              :validated="isValidated"
              :required="true"
              :isFocus="true"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Lnh vc kinh doanh (VD: Tp ha/ Dch v gii tr)"
              ref="txtDescription"
              v-model="organization.Description"
              :validated="isValidated"
              :isDisabled="false"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Slogan:"
              ref="txtSlogan"
              v-model="organization.Slogan"
              :validated="isValidated"
              :isDisabled="false"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Ch ca hng/ i l"
              ref="txtOwnerName"
              v-model="organization.OwnerName"
              :isDisabled="false"
              :onlyNumberChar="true"
            >
            </m-input>
          </div>
          <div class="m-col"></div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="S in thoi"
              ref="txtPhoneNumber"
              v-model="organization.PhoneNumber"
              :isDisabled="false"
              :required="true"
              :onlyNumberChar="true"
            >
            </m-input>
          </div>
          <div class="m-col">
            <m-input
              label="Email"
              ref="txtEmail"
              v-model="organization.Email"
              placeholder="VD: nvmanh@nmanh.com"
              :isDisabled="false"
              :required="true"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <div class="m-col">
            <m-input
              label="Giy php hot ng kinh doanh (nu c)"
              ref="txtBusinessLicense"
              v-model="organization.BusinessLicense"
              :isDisabled="false"
              :onlyNumberChar="true"
            >
            </m-input>
          </div>
        </div>
        <div class="m-row">
          <m-text-area
            label="a ch"
            ref="txtAddress"
            v-model="organization.Address"
          ></m-text-area>
        </div>
      </div>

      <!-- BC 3 - XC NHN THNG TIN NG K -->
      <div v-if="stepRegister == 3" id="step-3" class="step-3">
        <div class="license-info">
          <div class="license__icon"><i class="icofont-license"></i></div>
          <div class="license__content">
            <div>
              Cm n <b>{{ user.FullName }}</b
              >!
            </div>
            <div>
              Bn  ng k thnh cng <b>{{ organization.OrganizationName }}</b> v c
              tng 15 ngy dng th tnh nng VIP ca ng dng. <br />
              <i class="icofont-hand-right"></i> Vui lng nhp m xc nhn c gi n
              Email <b>{{ user.Email }}</b>  kch hot ti khon. <br />
              <i class="icofont-hand-right"></i> Tnh nng VIP s hot ng ngay sau khi
              bn xc nhn a ch Email.
            </div>
            <div class="confirm-box">
              <div class="confirm__form">
                <m-input
                  label="M xc nhn"
                  type="text"
                  v-model="tokenConfirm"
                  ref="txtConfirmToken"
                ></m-input>
                <m-button
                  id="btn-confirm"
                  text="Xc nhn"
                  @click="confirmAccount"
                ></m-button>
              </div>
            </div>
            <div>
              Bn c th ng k thnh vin VIP hoc gia hn VIP sau thi gian dng th
              ch vi 2000/ngy.
            </div>
          </div>
        </div>
        <div class="contant-info">
          Mi vn  cn h tr, xin vui lng lin h:
          <div class="author-name">Mnh Software</div>
          <li>
            <i class="icofont-mobile-phone"></i>:
            <a href="tel:0961179969">0961179969</a>
          </li>
          <li>
            <i class="icofont-ui-email"></i>:
            <a href="email:https://nmanh.com">manhnv229@gmail.com</a>
          </li>
          <li>
            <i class="icofont-web"></i>:
            <a href="web:https://nmanh.com">https://www.nmanh.com</a>
          </li>
        </div>
      </div>
    </template>
    <template v-slot:footer>
      <!-- CC BC THC HIN -->
      <div class="step-register">
        <el-steps :active="stepRegister" align-center>
          <el-step title="Bc 1" description="Ti khon" />
          <el-step title="Bc 2" description="Ca hng" />
          <el-step title="Bc 3" description="Hon thnh" />
        </el-steps>
      </div>
      <div class="footer-org">
        <div class="footer__button">
          <button
            v-if="stepRegister == 2"
            class="btn btn--default mg-left-10 btn-step"
            @click="onPrevStep"
          >
            <span class="step-icon"><i class="icofont-swoosh-left"></i></span>
            <span>Quay li</span>
          </button>
        </div>
        <div class="footer__button">
          <!-- <button class="btn btn--close" @click="onClose">Hy</button> -->
          <button
            v-if="stepRegister == 1"
            class="btn btn--default mg-left-10 btn-step"
            @click="onNextStep1"
          >
            <span>Bc tip theo</span>
            <span class="step-icon"><i class="icofont-swoosh-right"></i></span>
          </button>
          <button
            v-if="stepRegister == 2"
            class="btn btn--default mg-left-10 btn-step"
            @click="onNextStep2"
          >
            <span>Hon thnh</span>
            <span class="step-icon"><i class="icofont-swoosh-right"></i></span>
          </button>
          <button
            v-if="stepRegister == 3"
            class="btn btn--default mg-left-10 btn-step"
            @click="onLogin"
          >
            <span>ng nhp chng trnh</span>
            <span class="step-icon"><i class="icofont-login"></i></span>
          </button>
        </div>
      </div>
    </template>
  </m-page>
</template>
<script>
import router from "@/router";
import Enum from "@/scripts/enum";
export default {
  name: "OrganizationRegister",
  emits: ["onClose"],
  props: ["id"],
  computed: {
    titleByStep: function () {
      if (this.stepRegister == 1) {
        return "ng k ti khon";
      } else if (this.stepRegister == 2) {
        return "ng k ca hng";
      } else {
        return "Hon thnh ng k";
      }
    },
  },
  created() {
    // Ly m ca hng mi:
    this.maxios.get("organizations/new-code").then((res) => {
      this.organization.OrganizationCode = res;
    });
  },
  /* eslint-disable */
  methods: {
    confirmAccount() {
      var userConfirmToken = {
        UserName: this.user.UserName,
        TokenCode: this.tokenConfirm,
        Email: this.user.Email,
      };
      this.maxios
        .post("accounts/confirm", userConfirmToken, this.setFocusInputToken)
        .then(() => {
          this.isConfirmed = true;
          var msg = `Chc mng! <br> Bn  xc nhn thnh cng v nhn 15 ngy s dng tnh nng VIP.`;
          this.commonJs.showMessenger({
            title: "Xc nhn thnh cng",
            msg: msg,
            type: Enum.MsgType.Info,
            confirm: () => {
              this.commonJs.login(this.user.UserName, this.user.Password);
            },
          });
        });
    },
    onValidateRequiredInput(isValid, input) {
      console.log(isValid, input);
    },
    onClose() {
      router.push("/");
    },
    onSave() {
      var isValid = this.validateStepTwo();
      if (isValid) {
        this.user.Organization = this.organization;
        this.maxios
          .post(`accounts/register`, this.user, this.setFocusFirstInputErrors)
          .then(() => {
            this.stepRegister = 3;
          })
          .catch((res) => {
            console.log(res);
            const statusCode = res.status;
            if (statusCode == 400) {
              let errors = res.errors;
              let props = Object.keys(errors);
              var propErrors = [];
              for (const prop of props) {
                var erroText = errors[prop];
                switch (prop) {
                  case "UserName":
                    this.stepRegister = 1;
                    this.$refs.txtUserName.setInValidState(true, erroText);
                    propErrors.push(this.$refs.txtUserName);
                    break;
                  case "Password":
                    this.stepRegister = 1;
                    this.$refs.txtPassword.setInValidState(true, erroText);
                    propErrors.push(this.$refs.txtPassword);
                    break;
                  case "RePassword":
                    this.stepRegister = 1;
                    this.$refs.txtRePassword.setInValidState(true, erroText);
                    propErrors.push(this.$refs.txtRePassword);
                    break;
                  case "FullName":
                    this.stepRegister = 1;
                    this.$refs.txtFullName.setInValidState(true, erroText);
                    propErrors.push(this.$refs.txtFullName);
                    break;
                  case "Mobile":
                    this.stepRegister = 1;
                    this.$refs.txtUserPhoneNumber.setInValidState(true, erroText);
                    propErrors.push(this.$refs.txtUserPhoneNumber);
                    break;
                  case "Email":
                    this.stepRegister = 2;
                    this.$refs.txtEmail.setInValidState(true, erroText);
                    propErrors.push(this.$refs.txtEmail);
                    break;
                  case "OrganizationName":
                    this.stepRegister = 2;
                    this.$refs.txtOrganizationName.setInValidState(true, erroText);
                    propErrors.push(this.$refs.txtOrganizationName);
                    break;
                  default:
                    break;
                }
              }
              if (propErrors.length > 0) {
                this.propFirstNotValid = propErrors[0];
              }
            }
          });
      }
    },
    validateStepTwo() {
      var errorMsgs = [];
      var fieldsInValid = [];
      if (!this.commonJs.checkRequired(this.organization.OrganizationCode)) {
        let errorText = "<b>M ca hng/ i l</b> khng c  trng.";
        errorMsgs.push(errorText);
        this.$refs.txtOrganizationCode.setInValidState(true, errorText);
        fieldsInValid.push(this.$refs.txtOrganizationCode);
      }
      if (!this.commonJs.checkRequired(this.organization.OrganizationName)) {
        let errorText = "<b>Tn ca hng/ i l</b> khng c  trng.";
        errorMsgs.push(errorText);
        fieldsInValid.push(this.$refs.txtOrganizationName);
        this.$refs.txtOrganizationName.setInValidState(true, errorText);
      }
      if (!this.commonJs.checkRequired(this.organization.PhoneNumber)) {
        let errorText = "<b>S in thoi</b> khng c  trng.";
        errorMsgs.push(errorText);
        fieldsInValid.push(this.$refs.txtPhoneNumber);
        this.$refs.txtPhoneNumber.setInValidState(true, errorText);
      }
      if (!this.commonJs.checkRequired(this.organization.Email)) {
        let errorText = "<b>Email</b> khng c  trng.";
        errorMsgs.push(errorText);
        fieldsInValid.push(this.$refs.txtEmail);
        this.$refs.txtEmail.setInValidState(true, errorText);
      }
      if (!this.commonJs.checkEmailFormat(this.organization.Email)) {
        let errorText = "<b>Email</b> khng ng nh dng.";
        errorMsgs.push(errorText);
        fieldsInValid.push(this.$refs.txtEmail);
        this.$refs.txtEmail.setInValidState(true, errorText);
      }
      if (errorMsgs.length > 0) {
        this.commonJs.showMessenger({
          title: "D liu khng hp l",
          msg: errorMsgs,
          type: Enum.MsgType.Error,
          confirm: () => {
            if (fieldsInValid.length > 0) {
              fieldsInValid[0].setFocus();
            }
          },
        });

        return false;
      }
      return true;
    },
    validateStepOne() {
      var errorMsgs = [];
      var fieldsInValid = [];
      if (!this.commonJs.checkRequired(this.user.UserName)) {
        let errorText = "<b>Ti khon</b> khng c  trng.";
        errorMsgs.push(errorText);
        this.$refs.txtUserName.setInValidState(true, errorText);
        fieldsInValid.push(this.$refs.txtUserName);
      }
      if (!this.commonJs.checkRequired(this.user.Password)) {
        let errorText = "<b>Mt khu</b> khng c  trng.";
        errorMsgs.push(errorText);
        fieldsInValid.push(this.$refs.txtPassword);
        this.$refs.txtPassword.setInValidState(true, errorText);
      } else {
        if (this.user.Password != this.user.RePassword) {
          let errorText = "<b>Mt khu xc nhn</b> khng khp.";
          errorMsgs.push(errorText);
          fieldsInValid.push(this.$refs.txtRePassword);
          this.$refs.txtRePassword.setInValidState(true, errorText);
        }
      }
      if (!this.commonJs.checkRequired(this.user.FullName)) {
        let errorText = "<b>H v tn</b> khng c  trng.";
        errorMsgs.push(errorText);
        this.$refs.txtFullName.setInValidState(true, errorText);
        fieldsInValid.push(this.$refs.txtFullName);
      }
      if (!this.commonJs.checkRequired(this.user.Email)) {
        let errorText = "<b>Email</b> khng c  trng.";
        errorMsgs.push(errorText);
        this.$refs.txtUserEmail.setInValidState(true, errorText);
        fieldsInValid.push(this.$refs.txtUserEmail);
      }
      if (!this.commonJs.checkRequired(this.user.PhoneNumber)) {
        let errorText = "<b>S in thoi</b> khng c  trng.";
        errorMsgs.push(errorText);
        this.$refs.txtUserPhoneNumber.setInValidState(true, errorText);
        fieldsInValid.push(this.$refs.txtUserPhoneNumber);
      }
      if (errorMsgs.length > 0) {
        this.commonJs.showMessenger({
          title: "D liu khng hp l",
          msg: errorMsgs,
          type: Enum.MsgType.Error,
          confirm: () => {
            if (fieldsInValid.length > 0) {
              fieldsInValid[0].setFocus();
            }
          },
        });

        return false;
      }
      return true;
    },
    onPrevStep() {
      this.stepRegister -= 1;
    },
    onNextStep1() {
      var stepOneValid = this.validateStepOne();
      if (stepOneValid) {
        this.organization.Email = this.user.Email;
        this.organization.PhoneNumber = this.user.PhoneNumber;
        this.organization.OwnerName = this.user.FullName;
        this.stepRegister = 2;
        this.$nextTick(function () {
          this.$refs.txtOrganizationName.setFocus();
        });
      }
    },
    onNextStep2() {
      this.onSave();
    },
    onLogin() {
      this.commonJs.login(this.user.UserName, this.user.Password);
    },
    setFocusFirstInputErrors() {
      if (this.propFirstNotValid) {
        this.$nextTick(function () {
          this.propFirstNotValid.setFocus();
        });
      }
    },
    setFocusInputToken() {
      this.$refs.txtConfirmToken.setFocus();
    },
  },
  data() {
    return {
      registerTitle: "ng k ti khon",
      organization: {},
      user: {},
      isValidated: false,
      stepRegister: 1,
      propFirstNotValid: null,
      tokenConfirm: "",
      isConfirmed: false,
    };
  },
};
</script>
<style scoped>
.confirm-box {
  margin: 10px;
}

.confirm__form {
  max-width: 350px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  column-gap: 10px;
}

#btn-confirm {
  width: 100px;
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: 100px;
}

.footer-org {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  column-gap: 10px;
  margin-bottom: 10px;
}

.license-info {
  display: flex;
  align-items: center;
  column-gap: 10px;
  color: green;
  border: 1px dashed green;
  padding: 5px;
}
.license__icon {
  font-size: 30px;
}

#step-3 {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

.license__content {
  max-width: 600px;
}
.license--limitted {
  color: red;
}
.step-icon {
  font-size: 24px;
}

.btn-step {
  display: flex;
  align-items: center;
}

.author-name {
  font-weight: 700;
}

.step-register {
  margin-bottom: 10px;
}
</style>
