<template>
  <m-dialog title="Thng tin khch hng" @onClose="onClose">
    <template v-slot:content>
      <div class="m-row">
        <div class="m-col">
          <m-input
            label="M khch hng"
            ref="txtCustomerCode"
            v-model="customer.CustomerCode"
            :validated="isValidated"
            :required="true"
            :isDisabled="false"
            :isFocus="true"
          >
          </m-input>
        </div>
        <div class="m-col">
          <m-input
            label="Tn khch hng"
            ref="txtcustomerName"
            v-model="customer.FullName"
            :validated="isValidated"
            :required="true"
            :isDisabled="false"
          >
          </m-input>
        </div>
      </div>
      <div class="m-row">
        <div class="m-col">
          <m-combobox
            label="Nhm khch hng"
            v-model="customer.CustomerGroupId"
            url="/customergroups"
            propText="CustomerGroupName"
            propValue="CustomerGroupId"
          ></m-combobox>
        </div>
        <div class="m-col"></div>
      </div>
      <div class="m-row">
        <div class="m-col">
          <m-combobox
            label="Gii tnh"
            v-model="customer.Gender"
            url="/dictionarys/gender"
            propText="Text"
            propValue="Value"
          ></m-combobox>
        </div>
        <div class="m-col">
          <label for="">Ngy sinh:</label>
          <el-date-picker
            v-model="customer.DateOfBirth"
            type="date"
            format="DD-MM-YYYY"
          />
        </div>
      </div>
      <div class="m-row">
        <div class="m-col">
          <m-input
            label="S in thoi"
            v-model="customer.Mobile"
            :isDisabled="false"
            :onlyNumberChar="true"
          >
          </m-input>
        </div>
        <div class="m-col">
          <m-input
            label="Email"
            v-model="customer.Email"
            :isDisabled="false"
          >
          </m-input>
        </div>
      </div>
      <div class="m-row">
        <m-text-area label="a ch" v-model="customer.Address"></m-text-area>
      </div>
    </template>
    <template v-slot:footer>
      <button class="btn btn--close" @click="onClose">Hy</button>
      <button class="btn btn--default mg-left-10" @click="onSave">Lu</button>
    </template>
  </m-dialog>
</template>
<script>
import Enum from "@/scripts/enum";
export default {
  name: "CustomerDetail",
  emits: ["onClose"],
  props: ["id"],
  computed: {
    formMode: function () {
      if (this.id) {
        return Enum.FormMode.UPDATE;
      } else {
        return Enum.FormMode.ADD;
      }
    },
  },
  created() {
    if (this.id) {
      this.maxios.get(`customers/${this.id}`).then((res) => {
        this.customer = res;
      });
    } else {
      this.maxios.get(`customers/new-code`).then((res) => {
        this.customer.CustomerCode = res;
      });
    }
  },
  methods: {
    onClose() {
      this.$emit("onClose");
    },
    onSave() {
      if (this.formMode == Enum.FormMode.ADD) {
        this.maxios.post(`customers`, this.customer).then(() => {
          this.$emit("onClose");
        });
      } else {
        this.maxios.put(`customers/${this.id}`, this.customer).then(() => {
          this.$emit("onClose");
        });
      }
    },
  },
  data() {
    return {
      customer: {},
    };
  },
};
</script>
<style scoped></style>
